.container.home {
    font-size: @big;
    .main {
        height: e('calc(100% - 2rem)');
        overflow-x: hidden;
        overflow-y: auto;
        position: relative;
        .user {
            color: #fff;
            background-color: @blue;
            .info {
                height:  @size * 1.5;
                line-height: @size * 1.5;
                padding: 0 @space;
                .face {
                    width: @size * 1.5;
                    display: inline-block;
                    text-align: center;
                    i {
                        font-size: @size;
                        vertical-align: middle;
                    }
                }
            }
            .report {
                height:  @size * 1.5;
                display: flex;
                justify-content: center;
                align-items: center;
                border-bottom: 1px solid @border-gray;
                div {
                    width: 50%;
                    float: left;
                    position: relative;
                    &:first-child {
                        &:after {
                            content: "";
                            top: 15%;
                            right: 0;
                            width: 1px;
                            height: 70%;
                            background-color: #fff;
                            position: absolute;
                        }
                    }
                    p {
                        text-align: center;
                        &.score {
                            font-size: @xx-big;
                        }
                        &.text {
                            font-size: @normal;
                        }
                    }
                }
            }
        }
        .cell-list {
            padding: 0;
            margin: @space 0;
            border-top: 1px solid @border-gray;
            border-bottom: 1px solid @border-gray;
            li {
                overflow: hidden;
                span {
                    float: left;
                }
                .left,
                .right {
                    width: 10%;
                    text-align: center
                }
                .center {
                    width: 80%;
                    .count {
                        color: @font-gray;
                        float: right;
                    }
                }
            }
        }
    }
    .footer {
        position: relative;
        .btn {
            left: 50%;
            bottom: @space / 3;
            width: @size * 2.2;
            height: @size * 2.2;
            line-height: @size * 2;
            margin: 0;
            border: @space / 2 solid #fff;
            border-radius: 50%;
            position: absolute;
            transform: translateX(-50%);
        }
    }
}


